import Block1 from './Vip-BaseInfo-Block1.vue'
import Block2 from './Vip-BaseInfo-Block2.vue'
import detailPicker from './Detail-Picker.vue'
let cache = {};
export default {
    props: {
        info: {
            info1: {
                registerTime: '2015-01-12 14:12:12',
                origin: 'wechat',
                vipClass: '默认会员',
                vipTag: '夜猫子 白领'
            },
            info2: {
                score: 0,
                money: 0,
                coupon: 0
            }
        }
    },
    components: {
        Block1,
        Block2,
        detailPicker
    },
    data() {
        return {
            toggle: false,
        }
    },
    methods: {
        clickItem(val) {
            if (val.label == '余额') {
                this.toggle = !this.toggle;
                cache = val;
            } else if (val.label == '积分') {
                this.toggle = !this.toggle;
                cache = val;
            } else if (val.label == '优惠券') {
                this.$emit('click', {
                    ...val
                })
            }
        },
        clickMenu(type) {
            this.toggle = !this.toggle;
            if (type == 'add' || type == 'minus') {
                this.$emit('click', {
                    type,
                    ...cache
                })
            } else if (type == 'clear') { //清空需要二次确认
                this.Dialog.confirm({
                    title: '清空' + cache.label,
                    message: '您确认将该用户的' + cache.label + '清零吗？'
                }).then(() => {
                    this.$emit('click', {
                        type,
                        ...cache
                    })
                }).catch(() => {
                    // on cancel
                });
            }
        }
    },
}
